<template>
  <main class="box-border h-full min-h-[calc(100vh-84px)] w-full pb-[84px]">
    <C-Back-Nav :title="'設計師業績分析報表'" :routeTarget="{name: 'Report'}"></C-Back-Nav>
    <section class="relative z-0 mt-[52px] p-3">
      <div class="flex gap-2">
        <C-Select class="w-full" :list="year" v-model="dateYYYY" :placeholder="'年'" :itemText="'label'" :itemValue="'value'"></C-Select>
        <C-Select class="w-full" :list="month" v-model="dateMM" :placeholder="'月'" :itemText="'label'"></C-Select>
      </div>

      <div class="text-left text-sm">
        <div class="my-4 flex items-center justify-between">
          <div class="flex items-baseline">
            <p class="mr-1 text-[24px] font-bold">{{dateYYYY}}年</p>
            <p>{{dateMM.value}}月</p>
          </div>
          <div>
            <p>設計師 {{'Alex'}}</p>
          </div>
        </div>
        <div v-if="isReviewResult">
          <!-- eslint-disable-next-line -->
          <div v-for="(reviewItem, index) in reviewResult" :key="`reviewItem${index}`" class="mb-1 flex cursor-pointer items-center justify-between border-b border-solid border-primary/50 py-1 ">
            <div class="w-2/12 pl-1">{{+index+1 > 10 ? +index+1 :`0${+index+1}`}}</div>
            <div class="w-7/12">{{reviewItem.label}}</div>
            <div class="w-3/12 text-right">${{reviewItem.price}}</div>
          </div>
        </div>
        <div class="mt-3 flex items-baseline justify-between text-second">
          <p>總應領薪資</p>
          <p class="text-[28px]">${{totalCount(reviewResult)}}</p>
          </div>
      </div>
    </section>
  </main>
</template>

<script>
import year from '@/assets/constant/year';
import month from '@/assets/constant/month';

export default {
  name: 'priceList-index',
  description: '報表的設計師業績分析報表',
  metaInfo: {
    title: '名留',
    titleTemplate: '%s | 設計師業績分析報表',
  },
  data() {
    return {
      year,
      month,
      date: '',
      dateYYYY: '2022',
      dateMM: { label: '四月', value: 4 },
      reviewResult: [
        {
          label: '務虛業績金額:',
          price: '45000',
        },
        {
          label: '扣業績－付款手續費:',
          price: '3200',
        },
        {
          label: '扣業績－扣項:',
          price: '3200',
        },
        {
          label: '服務實業績金額:',
          price: '5000',
        },
        {
          label: '實業績－指定:',
          price: '45000',
        },
        {
          label: '實業績－不指定:',
          price: '35000',
        },
        {
          label: '扣業績－使用互助:',
          price: '3200',
        },
        {
          label: '服務實業績 扣使用互助:',
          price: '3200',
        },
      ],
    };
  },
  methods: {
    doRoute() {
      this.$router.push({ name: 'Punch' });
    },
  },
  computed: {
    isReviewResult() {
      return this.reviewResult.length;
    },
    totalCount() {
      return (item) => {
        const reducer = (accumulator, curr) => +accumulator + +curr;
        const priceList = item.map((itemPrice) => itemPrice.price);
        return priceList.reduce(reducer);
      };
    },
    currentMM() {
      let currentMonth = '';
      if (this.date) {
        currentMonth = this.$dayjs(this.date).format('MM');
      } else {
        currentMonth = this.$dayjs(new Date()).format('MM');
      }
      return currentMonth;
    },
    currentYYYY() {
      let currentYear = '';
      if (this.date) {
        currentYear = this.$dayjs(this.date).format('YYYY');
      } else {
        currentYear = this.$dayjs(new Date()).format('YYYY');
      }
      return currentYear;
    },
  },
};
</script>

